@font-face {
  font-family: nunito;
  src: url(/static/media/Nunito-Light.abdadce6.ttf);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: nunito;
}

.container {
  width: 100%;
  height: 100vh;
  margin: auto;
  overflow: auto;
  min-height: 300px;
  
  border-radius: 5px;
  position: relative;
}

.loginBox {
  text-align: center;
  position: absolute;
  /* border: 1px solid steelblue; */
  
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 40rem;
  height: 33rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

}

.login-container{
  margin-top: 15%;
}
.signup-header{
  margin-bottom: 15px;
}
.signup-header h1{
  font-size: 45px;
}
.signup-header h2{
  margin-top: 8px;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #36A7F9;
  color: #fff;
  border: none;
  padding: 15px 20px;
  border-radius: 10px;
  cursor: pointer;
  text-decoration: none;
  font-size: 25px;
  /* font-family: inherit; */
  width: 57%;

}

.btn:focus {
  outline: none;
}

.btn:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}



.add-form {
  margin-bottom: 40px;
}



.form-control input {
  width: 57%;
  height: 43px;
  /* margin: 5px; */
  margin-top: 17px;
  padding: 3px 7px;
  font-size: 17px;
  background-color: rgb(245, 245, 245);
  border-style:solid;
  border-radius: 10px;
  border-width: 0.5px;
  border-color: rgb(226, 226, 226);
}
.form-control input:focus{
  outline:none;
}

.form-control-check {
  width: 57%;
}

.form-control-check label {
  /* flex: 1; */
}

.form-control-check input {
  /* flex: 2; */
  /* vertical-align:middle; */
  /* margin-left: 0; */
  /* height: 15px; */
  /* display: inline-block; */
}

footer {
  margin-top: 30px;
  text-align: center;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

